/**
 * Основные стили
 **/
html {
  overflow-x: hidden;
  font-size: 16px;
  color: black;
  background-color: white; }

body {
  font-family: "Roboto", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.main {
  min-width: 320px;
  max-width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #fff; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.header {
  margin-bottom: 50px;
  margin-top: 50px; }

.header_menu {
  padding: 0 0 0 0; }

.header_call {
  align-items: flex-end;
  margin-right: 20px; }

ul.header_menu li {
  display: inline; }

.header_menu_link {
  font-size: 16px;
  text-decoration: none;
  color: black; }

.header_menu_link:hover {
  color: #fbaec0;
  text-decoration: underline;
  transition: 1s; }

.header_menu_element {
  font-size: 16px;
  padding-left: 20px; }

.header_call span {
  font-size: 20px;
  margin-right: 20px; }

@media (max-width: 1200px) {
  .header_call {
    display: none; } }

o {
  position: relative; }

.info_ellipse {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 814px;
  height: 630px;
  overflow: hidden; }

.info_block {
  width: 100%;
  max-width: 1250px;
  padding: 0 40px;
  margin: 0 auto; }

.info_block_logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  flex-direction: row-reverse;
  z-index: 2; }

.info_block_logo_foto {
  width: 435px;
  position: relative;
  right: -81px; }

.info_block_block {
  flex-grow: 1; }

.info_block_hgroup {
  margin: -60px 0 46px; }

.info_block_name {
  color: #1f1f24;
  margin-bottom: 22px;
  font-weight: 800;
  font-size: 80px; }

.info_block_title {
  color: #8993ad;
  font-size: 25px;
  font-weight: 400; }

.info_block_btn {
  width: 270px;
  height: 60px;
  line-height: 58px;
  background-color: #ff4e2e;
  border: 1px solid #ff4e2e;
  color: #fff;
  text-align: center;
  font-size: 18px;
  display: block;
  transition: 0.25s ease;
  cursor: pointer; }

.info_block_btn:hover {
  background-color: #fff;
  color: #ff4e2e; }

@media only screen and (max-width: 1249px) {
  .info_block_name {
    font-size: 54px;
    margin-bottom: 25px; }
  .info_ellipse {
    right: -170px;
    width: 678px;
    height: 524px; } }

@media only screen and (max-width: 770px) {
  .info_block_logo_foto img {
    width: 100%;
    height: auto; }
  .info_ellipse svg {
    width: 400px;
    height: auto; }
  .info_ellipse {
    width: 434px;
    height: auto;
    left: 80px;
    top: 76px; }
  .info_block_btn {
    width: 100%;
    height: 55px;
    line-height: 53px;
    font-size: 16px; }
  .info_block_block {
    margin-bottom: 20px;
    padding: 8px 20px 0;
    text-align: center; }
  .info_block_logo {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .info_block_block {
    padding: 18px 20px 0;
    text-align: center; }
  .info_block_logo_foto {
    width: 224px;
    margin: 0 auto;
    right: -29px;
    padding-top: 50px; }
  .info_block_hgroup {
    margin: 0 0 25px; }
  .info_block_name {
    margin-bottom: 12px;
    font-size: 30px; }
  .info_block_title {
    font-size: 16px;
    line-height: 24px; }
  .info_block {
    margin: 0 auto; } }

.flying-el {
  position: absolute;
  color: #fa0d8a;
  font-weight: bold; }

.flying-el > div {
  position: absolute; }

.flying-el__brackets {
  font-size: 48px;
  transform: rotate(10deg);
  left: 63px;
  top: 20px;
  transition: 1s;
  animation: fadeIn__brackets 2s ease-in-out; }

@keyframes fadeIn__brackets {
  0% {
    left: -1000px;
    opacity: 0.1; }
  70% {
    opacity: 0.4; }
  100% {
    left: -203px;
    opacity: 1; } }

.flying-el__hashtag {
  font-size: 57px;
  transform: rotate(15deg);
  left: 206px;
  top: 376px;
  animation: fadeIn__hashtag 2s ease-in-out; }

@keyframes fadeIn__hashtag {
  0% {
    left: -1000px;
    opacity: 0.1; }
  70% {
    opacity: 0.4; }
  100% {
    left: -96px;
    opacity: 1; } }

.flying-el__slash {
  font-size: 40px;
  transform: rotate(-20deg);
  left: 700px;
  top: 575px;
  animation: fadeIn__slash 2s ease-in-out; }

@keyframes fadeIn__slash {
  0% {
    top: 2000px;
    opacity: 0.1; }
  90% {
    opacity: 0.4; }
  100% {
    top: 475px;
    opacity: 1; } }

.flying-el__div_size_s {
  font-size: 30px;
  transform: rotate(-15deg);
  top: 20px;
  left: 614px;
  animation: fadeIn__div_s 2s ease-in-out; }

@keyframes fadeIn__div_s {
  0% {
    top: -1000px;
    opacity: 0.1; }
  70% {
    opacity: 0.4; }
  100% {
    top: 170px;
    opacity: 1; } }

.flying-el__div_size_m {
  font-size: 37px;
  transform: rotate(20deg);
  position: absolute;
  top: 161px;
  left: 980px;
  animation: fadeIn__div_m 2s ease-in-out; }

@keyframes fadeIn__div_m {
  0% {
    top: 1700px;
    opacity: 0.1; }
  70% {
    opacity: 0.4; }
  100% {
    top: 561px;
    opacity: 1; } }

.flying-el-service {
  position: relative;
  display: none; }

.flying-el-service > div {
  color: #fff;
  font-weight: bold;
  position: absolute; }

.flying-el-service__brackets {
  font-size: 50px;
  top: 59px;
  left: -200px;
  transform: rotate(25deg); }

.flying-el-service__hashtag {
  font-size: 58px;
  top: 260px;
  left: -100px;
  transform: rotate(15deg); }

.flying-el-service__div {
  font-size: 38px;
  top: 307px;
  left: 1050px;
  transform: rotate(35deg); }

.flying-el-service__slash {
  font-size: 40px;
  top: 50px;
  left: 1200px;
  transform: rotate(-25deg); }

.flying-el-service__brackets:last-child {
  top: 217px;
  right: -273px;
  transform: rotate(-177deg); }
