@charset "UTF-8";

/**
 * Основные стили
 **/

html {
  overflow-x: hidden;
  font-size: 16px;
  color: black;
  background-color: white;
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.main {
  @include min-max(320px, 100%, 15px);
  background-color: $white;
}

.wrap {
  @include min-max(320px, 1200px, 15px);
}
